/* 
* Start user theme 
*/
:root {
  --primary: #6c757d !important;
  --secondary: #e8f0fe !important;
  --light: #f8f9fa !important;
  --white: #fff !important;
  --dark: #6c757d !important;
  --gray-dark: #343a40 !important;
  --active: #f0f1f2 !important;
  --disabled: #e9ecef !important;
  --danger: #dc3545 !important;
  --info: #17a2b8 !important;
}

* {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  display: flex;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
}

body,
html,
.App,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

@media (min-width: 768px) {
  .auth-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: left;
    padding-bottom: 5%;
  }
}

.auth-inner {
  max-width: 500px;
  width: 100%;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 3px 10px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all 0.3s;
}

.auth-wrapper .form-control:focus {
  border-color: #6c757d !important;
  border-color: var(--primary) !important;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.other-options,
.other-options a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

/* .other-options a {
  color: #167bff;
} */

/** 
* For xl-screens: Divide screen
*/
@media (min-width: 1200px) {
  body {
    background: linear-gradient(
      to right,
      #e8f0fe 33.33%,
      #f8f9fa 33.33%,
      #f8f9fa 83.33%,
      #6c757d 83.33%,
      #6c757d 100%
    );
    background: linear-gradient(
      to right,
      var(--secondary) 33.33%,
      var(--light) 33.33%,
      var(--light) 83.33%,
      var(--primary) 83.33%,
      var(--primary) 100%
    );
  }
}

/** 
* For less than xl-screens: show pattern
*/
@media (max-width: 1200px) {
  body {
    background: linear-gradient(
      to right,
      #e8f0fe 50%,
      #f8f9fa 50%
    );
    background: linear-gradient(
      to right,
      var(--secondary) 50%,
      var(--light) 50%
    );
  }
}

/** 
* For less than xl-screens: show pattern
*/
@media (max-width: 992px) {
  body {
    background: repeating-linear-gradient(
      to right,
      #f7f7f7,
      #f7f7f7 10px,
      #e8f6f3 10px,
      #e8f6f3 20px
    );
  }
}

.btn-primary {
  color: #f8f9fa !important;
  color: var(--light) !important;
  background-color: #6c757d !important;
  background-color: var(--dark) !important;
  border-color: #6c757d !important;
  border-color: var(--dark) !important;
}

.btn-outline-primary {
  color: #6c757d !important;
  color: var(--dark) !important;
  background-color: var(--scondary) !important;
  border-color: #6c757d !important;
  border-color: var(--dark) !important;
}

.btn-outline-primary:hover {
  color: #f8f9fa !important;
  color: var(--light) !important;
  background-color: #6c757d !important;
  background-color: var(--dark) !important;
  border-color: #6c757d !important;
  border-color: var(--dark) !important;
}

.list-group-item {
  color: #6c757d !important;
  color: var(--dark) !important;
}

.list-group-item.active {
  background-color: #f0f1f2 !important;
  background-color: var(--active) !important;
  border-color: #f0f1f2 !important;
  border-color: var(--active) !important;
}

a {
  color: #343a40 !important;
  color: var(--gray-dark) !important;
}

a.nav-link-2 {
  color: #f8f9fa !important;
  color: var(--light) !important;
}

a:hover {
  color: #343a40 !important;
  color: var(--gray-dark) !important;
}

a.nav-link-2:hover {
  color: #fff !important;
  color: var(--white) !important;
}

.bg {
  /* Full height */
  min-height: 100vh;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.page-item.active .page-link {
  z-index: 3;
  color: #fff;
  background-color: #6c757d !important;
  background-color: var(--dark) !important;
  border-color: #6c757d !important;
  border-color: var(--dark) !important;
}

.button-transparent {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}

.no-focus:focus {
  outline: 0 !important;
}

input[readonly] {
  -webkit-touch-callout: none !important;
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}

.custom-control-input:checked ~ .custom-control-label::before {
  color: #fff;
  border-color: #6c757d !important;
  border-color: var(--dark) !important;
  background-color: #6c757d !important;
  background-color: var(--dark) !important;
  /* font-size: 0.5rem; */
}

.custom-control-input:disabled ~ .custom-control-label:before,
.custom-control-input[disabled] ~ .custom-control-label:before {
  border-color: #e9ecef !important;
  border-color: var(--disabled) !important;
  background-color: #e9ecef !important;
  background-color: var(--disabled) !important;
}

.custom-range {
  color: #6c757d !important;
  color: var(--dark) !important;
  outline: none; /* Remove outline */
}

.custom-range::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  background: #6c757d !important;
  background: var(--dark) !important; /* Green background */
}

.custom-range::-moz-range-thumb {
  background: #6c757d !important;
  background: var(--dark) !important; /* Green background */
}

.link {
  cursor: pointer;
}

.invalid-feedback-custom {
  display: block;
  width: 100%;
  margin-top: 0.25rem;
  font-size: 80%;
  color: #dc3545;
}

/* 
* End user theme 
*/

.auth-wrapper-summary {
  display: block;
  justify-content: center;
  flex-direction: row;
  text-align: left;
  margin-top: 0;
  padding-top: 0%;
  /* padding-right: 10px; */
  /* margin-bottom: 30px; */
}

.auth-inner-summary {
  width: 100%;
  max-width: 700px; /* 700px */
  margin: auto;
  background: #fbfcfc; /* #ccd1d1; */
  /* box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2); */
  padding: 1vh 1vw;
  border-radius: 5px;
  transition: all 0.3s;
}

.autoWidth {
  max-width: 500px;
  transition: max-width 0.5s ease;
}

.custom-card.card-header {
